to these RNA components of the translation system, the chloroplast genome encodes about 20 ribosomal proteins, which represent approximately a third of the proteins of chloroplast ribosomes. Some subunits of RNA polymerase are also encoded by chloroplasts, although additional RNA polymerase subunits and other factors needed for chloroplast gene expression are encoded in the nucleus. The chloroplast genome also encodes approximately 30 proteins that are involved in photosynthesis, including components of photosystems I and II, of the cytochrome bf complex, and of ATP synthase. In addition, one of the subunits of ribulose bisphosphate carboxylase (rubisco) is encoded by chloroplast DNA. Rubisco is the critical enzyme that catalyzes the addition of CO2 to ribulose-1,5-bisphosphate during the Calvin cycle. Not only is it the major protein component of the chloroplast stroma, but it is also thought to be the single most abundant protein on Earth, so it is noteworthy that one of its subunits is encoded by the chloroplast genome.")]),_vm._m(1),_c('h3',[_vm._v("Database Features")]),_vm._m(2)],1),_c('section',{staticClass:"sidebar"},[_c('section',[_c('h3',[_vm._v("Search")]),_c('p',[_vm._v("Enter the NC Number to direct access")]),_c('el-input',{attrs:{"name":"ncNumber","id":"ncNumber","type":"text","tabindex":"0","placeholder":"NC_000932.1","clearable":"","autofocus":""},model:{value:(_vm.input),callback:function ($$v) {_vm.input=$$v},expression:"input"}}),_c('div',{staticClass:"buttons"},[_c('el-button',{staticClass:"special",on:{"click":function($event){return _vm.toSingleGene(_vm.input===''?'NC_000932.1':_vm.input)}}},[_vm._v("GO")]),_c('router-link',{attrs:{"to":_vm.toSlObj,"tag":"el-button"}},[_vm._v("View List")])],1)],1),_c('hr',{attrs:{"size":"1"}}),_c('section',[_vm._m(3),_c('h3',[_vm._v("Examples")]),_c('p',[_vm._v("\n          All the plastid DNAs have their own web page, you can visit the\n          "),_c('router-link',{attrs:{"to":"/intro"}},[_vm._v("Introduct